@import url(https://fonts.gogleapis.com/css?family=Lato:400,700,900);
* {
  padding: 0px;
  margin: 0px;
  font-family: Lato, sans-serif;
  -drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}
::-webkit-scrollbar {
  display: none;
}
body {
  background-color: #c8c8c8;
  overflow: scroll;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 98%;
  height: 96vh;
  background-color: #fdfdfd;
  transform: translate(1%, 2vh);
  /* flex properties*/
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

@media (max-width: 780px) {
  .app {
    overflow: scroll;
    flex-direction: column;
    justify-content: initial;
  }
}

@media (max-width: 360px) {
  .app {
    overflow: scroll;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10%;
}
.footer > button {
  cursor: pointer;
  outline-width: 0px;
  width: 150px;
  height: 45px;
  border: none;
  border-radius: 30px;
  font-size: 14px;
  font-weight: 800;
  background: rgba(95, 173, 185, 1);
  color: white;
}

.header {
  width: 100%;
  height: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.header > h4 {
  font-weight: 900;
  font-size: 16px;
  margin-top: 20px;
}
.header > span {
  font-size: 18px;
}
.header > p {
  font-size: 46px;
  font-weight: 900;
}
.header > p::before {
  content: "$";
  position: relative;
  top: -10px;
  font-size: 28px;
}
.header > p::after {
  content: "mo";
  font-size: 28px;
}

.main {
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main__feature {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 2px;
  width: 70%;
  height: 50px;
  text-align: center;
}
.main__feature > img {
  margin-left: 10px;
  width: 20px;
  height: 20px;
}
.main__feature > p {
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 10px;
}
@media (max-width: 1000px) {
  .main__feature {
    width: 100%;
    /* background-color: antiquewhite; */
  }
}
@media (max-width: 780px) {
  .main {
    justify-content: center;
  }
  .main__feature {
    width: 70%;
  }
}

@media (max-width: 400px) {
  .main {
    justify-content: center;
  }
  .main__feature {
    width: 100%;
  }
}

.table {
  width: 28%;
  height: 85vh;
  background: rgb(57, 165, 182);
  background: linear-gradient(
    to bottom,
    rgba(57, 165, 182, 1) 12%,
    rgba(95, 173, 185, 1) 37%,
    rgba(118, 204, 218, 1) 86%
  );
  box-shadow: 0 10px 10px rgb(202, 200, 200);
  z-index: 2;
  border-radius: 10px;
  transition: 1s ease-in-out;
}

@media (max-width: 780px) {
  .table {
    width: 70%;
    margin-top: 20px;
    height: 550px;
  }
}

@media (max-width: 360px) {
  .table {
    width: 95%;
    margin-top: 20px;
    height: 550px;
  }
}

